<template>
	<view>
		<view class="authorization-button" @tap="getAuthorization">
			獲取您的授權信息
		</view>
		<view class="authorization-button" @tap="getAuthorization(true)">
			跳過授權信息
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		onLoad() {
			
		},
		methods: {
			// 获取用户信息
						getUserInfo(item) {
							uni.request({
								url: this.$api + 'leaguer/userinfo',
								method: 'GET',
								header: {
									'Authorization': 'Bearer ' + item.auth_token
								},
								success: (res) => {
									console.log(res);
								}
							})
						},
			getAuthorization(bol) {
				if (bol == true) {
					uni.navigateTo({
						url: './index'
					})
				} else {
					// #ifdef H5
					uni.navigateTo({
						url: './index'
					})
					// #endif
					// #ifdef MP
					uni.getUserProfile({
						desc: 'Wexin', // 这个参数是必须的  
						success: res => {
							console.log(res);
						},
						fail: err => {
							console.log(err)
						}
					})
					// #endif
				}
			}
		}
	}
</script>

<style lang="scss">
	.authorization-button {
		width: 360upx;
		height: 100upx;
		line-height: 100upx;
		text-align: center;
		background: #07C160;
		border-radius: 25px;
		margin: auto;
		margin-top: 50upx;
	}
</style>
